/* SHIMMER STYLES */
@keyframes placeHolderShimmer {
  0% {
    background-position: 100% 0; }
  100% {
    background-position: -100% 0; } }

@keyframes placeHolderShimmerWide {
  0% {
    background-position: 200% 0; }
  100% {
    background-position: -200% 0; } }

.loading, .loading-2 {
  position: relative; }

.loading-img:after {
  content: '';
  animation-duration: 4s;
  animation-fill-mode: forwards;
  animation-iteration-count: infinite;
  animation-name: placeHolderShimmerWide;
  animation-timing-function: linear;
  background: linear-gradient(to right, #eeeeee 8%, #dddddd 18%, #eeeeee 33%);
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  background-size: 400% 100%; }

.loading:after {
  content: '';
  animation-duration: 1.5s;
  animation-fill-mode: forwards;
  animation-iteration-count: infinite;
  animation-name: placeHolderShimmer;
  animation-timing-function: linear;
  background: linear-gradient(to right, #eeeeee 8%, #dddddd 18%, #eeeeee 33%);
  position: absolute;
  left: 0;
  top: 0;
  right: 25%;
  bottom: 0;
  background-size: 200% 100%; }

.loading-2:before, .loading-2:after {
  content: '';
  animation-duration: 1.5s;
  animation-fill-mode: forwards;
  animation-iteration-count: infinite;
  animation-name: placeHolderShimmer;
  animation-timing-function: linear;
  background: linear-gradient(to right, #eeeeee 8%, #dddddd 18%, #eeeeee 33%);
  position: absolute;
  height: 40%;
  left: 0;
  background-size: 200% 100%; }

.loading-2:before {
  right: 0;
  top: 5%; }

.loading-2:after {
  right: 10%;
  top: 55%; }

